import { FormControl } from '@angular/forms'
import { Injectable } from '@angular/core';

@Injectable()
export class EmailValidator {
   static isValid (control: FormControl) {
       const re = /^([a-zA-Z0-9_\-\.]+)@([a-zA-Z0-9_\-\.]+)\.([a-zA-Z]{2,5})$/
       .test(control.value);
       if (re) {
           return null;
       } else {
           return {
               'invalidEmail':true
           }
       }
   }
}


